














import Vue from "vue";

export default Vue.extend({
  props: {
    text: {
      type: String,
      required: true,
    },
    isFooter: {
      type: Boolean,
      required: false,
    },
    isSupport: {
      type: Boolean,
      required: false,
    },
    isPrimary: {
      type: Boolean,
      required: false,
    },
    link: {
      type: String,
      required: true,
    },
  },
  methods: {
    handleClick() {
      window.location.replace(this.link);
    },
  },
});
